.sr-only { position: absolute; width: 1px; height: 1px; padding: 0; margin: -1px; overflow: hidden; clip: rect(0, 0, 0, 0); border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus { position: static; width: auto; height: auto; margin: 0; overflow: visible; clip: auto; }

/* ---------------------------------------------------------------------------- Structure ------------------------------------------------------------------------- */
.css-spinner { width: 40px; height: 40px; background-color: #F00036; margin: 100px auto; animation: sk-rotateplane 1.2s infinite ease-in-out; }

@keyframes sk-rotateplane { 0% { transform: perspective(120px) rotateX(0deg) rotateY(0deg); }
  50% { transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg); }
  100% { transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg); } }

#back-to-top { position: fixed; background-image: url("./assets/img/totop.svg"), none; width: 48px; height: 48px; display: inline-block; bottom: 10px; right: 10px; z-index: 9999; }

#back-to-top:hover { background-image: url("./assets/img/totop-white.svg"), none; }

.page-overview .page-overview__wrapper { max-width: 600px; margin: 0 auto; }

/* ---------------------------------------------------------------------------- Structure ------------------------------------------------------------------------- */
/* global box-sizing */
*, *:after, *:before { -moz-box-sizing: border-box; box-sizing: border-box; -webkit-font-smoothing: antialiased; font-smoothing: antialiased; text-rendering: optimizeLegibility; }

/* html element 62.5% font-size for REM use i.e. 1rem = 10px*/
html { font-size: 62.5%; height: 100%; min-height: 100%; }

body { font-family: 'PT Sans Narrow', sans-serif; font: 400 11px/1.4 sans-serif; margin: 0; height: 100%; min-height: 100%; }

/* clear */
.clear:before, .clear:after { content: ' '; display: table; }

.clear:after { clear: both; }

.clear { clear: both; *zoom: 1; }

img { max-width: 100%; vertical-align: bottom; height: auto; }

a { color: #444; text-decoration: none; }

a:hover { color: #444; }

a:focus { outline: 0; }

a:hover, a:active { outline: 0; }

select { width: 100%; }

.container { padding: 0px; }

/* WPCore */
/* ---------------------------------------------------------------------------- WordPress Core Styles ------------------------------------------------------------------------- */
.alignnone { margin: 5px 20px 20px 0; }

.aligncenter, div.aligncenter { display: block; margin: 5px auto 5px auto; }

.alignright { float: right; margin: 5px 0 20px 20px; }

.alignleft { float: left; margin: 5px 20px 20px 0; }

a img.alignright { float: right; margin: 5px 0 20px 20px; }

a img.alignnone { margin: 5px 20px 20px 0; }

a img.alignleft { float: left; margin: 5px 20px 20px 0; }

a img.aligncenter { display: block; margin-left: auto; margin-right: auto; }

.wp-caption { max-width: 100%; padding: 2rem; text-align: center; }

.wp-caption.alignnone { margin: 5px 20px 20px 0; }

.wp-caption.alignleft { margin: 5px 20px 20px 0; }

.wp-caption.alignright { margin: 5px 0 20px 20px; }

.wp-caption img { border: 0 none; height: auto; margin: 0; max-width: 100%; padding: 0; width: auto; }

.wp-caption .wp-caption-text, .gallery-caption { margin: 0; padding: 0.5rem; }

.textwidget img { margin: 1rem 0; }

.bypostauthor { background: inherit; }

/* ---------------------------------------------------------------------------- Recommended sizes ------------------------------------------------------------------------- */
.size-auto, .size-full, .size-large, .size-medium, .size-thumbnail { max-width: 100%; height: auto; }

.clear--both { clear: both; }

/* Base */
/** Base SCSS */
html { font-size: 16px; }

h1, h2, h3, h4, h5, h6 { color: #07074e; font-weight: 600; padding: 10px 0px; }

h1 { color: #910811; font-size: 3.5rem; }

@media (max-width: 767px) { h1 { font-size: 3rem; } }

h2 { font-size: 3rem; }

h3 { font-size: 2.5rem; }

h4 { font-size: 2rem; }

h5 { font-size: 1.5rem; }

h6 { font-size: 1rem; font-weight: 600; }

p { margin-bottom: 25px; font-size: 1.6rem; }

a:hover { color: #910811; }

body { min-height: 1000px; padding-top: 70px; font-family: 'PT Sans Narrow', sans-serif; font-size: 2rem; color: #666; }

.row--padded { padding: 7rem 0; }

.row--padded-top { padding-top: 7rem; }

.row--padded-bottom { padding-bottom: 7rem; }

.vertical-align { display: flex; align-items: center; justify-content: center; flex-direction: row; }

#curtain-slider { height: 800px; height: 70vh; }

@media (min-width: 768px) { #curtain-slider { height: 90vh; } }

#curtain-slider #btn-wrapper .request-toggle { visibility: visible !important; }

@media (max-width: 767px) { #curtain-slider #btn-wrapper .request-toggle:after { display: none; } }

.round { border-radius: 50%; border: 1px solid #07074e; }

.search-dropdown, .sub-dropdown { display: block; width: 100%; padding: 0 100px; background: rgba(255, 255, 255, 0.9); z-index: 999999999999 !important; position: fixed; top: 0 !important; border-top: 2px solid #d1d1d1; border-bottom: 2px solid #d1d1d1; }

.request-dropdown-mobile { display: none; }

/* Buttons */
.elm-button { border: 5px solid #07074e; background-color: transparent; color: #07074e; padding: 10px 40px; font-weight: 400; font-size: 1.35rem; transition: .2s ease; border-radius: 0; font-family: 'PT Sans Narrow',sans-serif; border-radius: 2px; }

.elm-button:hover, .elm-button:active, .elm-button:focus { background-color: #07074e; color: white; }

.button { border: 5px solid #910811; background-color: #910811; color: #fff; padding: 10px 40px !important; font-weight: 400; font-size: 1.35rem !important; transition: 0.2s ease !important; font-family: 'PT Sans Narrow',sans-serif; border-radius: 2px; box-shadow: 1px 2px 3px rgba(7, 7, 78, 0.2); }

.button:after { content: "\f105"; font-family: FontAwesome; margin-left: 10px; }

.button:hover { border: 5px solid #910811; background-color: white; color: #910811; box-shadow: 1px 2px 3px rgba(7, 7, 78, 0.6); text-decoration: none; }

.button i { font-size: 1.35rem; }

.button--arrow:after { content: "\f054"; font-family: FontAwesome; margin-left: 10px; }

.button--red-outline { border: 5px solid #910811; background-color: transparent; color: #910811; }

.button--red-outline:hover { border: 5px solid #910811; background-color: #910811; color: white; }

.button--white { border: 5px solid white; background-color: transparent; color: white; }

.button--white:hover { border: 5px solid white; background-color: white; color: #910811; }

.button--white-blue { border: 5px solid white; background-color: #07074e; color: white; }

.button--white-blue:hover { border: 5px solid white; background-color: transparent; color: #07074e; }

.button--blue { border: 5px solid #07074e; background-color: white; color: #07074e; }

.button--blue:hover { border: 5px solid #07074e; background-color: #07074e; color: white; }

.button--blue-outline { border: 5px solid #07074e; background-color: transparent; color: #07074e; }

.button--blue-outline:hover { border: 5px solid #07074e; background-color: #07074e; color: white; }

.button .no-background { background-color: none !important; color: blue; }

/* Header and Footer */
.navbar-header img { height: 37px; width: 144px; }

.navbar-brand { height: auto; padding: 0 15px; }

.navbar { padding: 1.2rem 15px; }

.navbar-right { margin-right: 0 !important; }

.navbar-default { background-color: white; }

.navbar-default .navbar-nav > li > a { color: #07074e; font-size: 1.35rem; padding: 0 15px; }

@media (min-width: 768px) and (max-width: 991px) { .navbar-default .navbar-nav > li > a { padding-left: 10px; padding-right: 10px; font-size: 1.3rem; } }

.navbar-default .navbar-nav > li > a > i { color: #07074e !important; }

.navbar-default .navbar-nav > li > a > i:hover { color: #910811 !important; }

.navbar-default .navbar-nav > .active > a, .navbar-default .navbar-nav > .active > a:hover, .navbar-default .navbar-nav > .active > a:focus { color: #910811; background-color: white; text-decoration: underline; }

.navbar-default .navbar-nav > li > a:hover, .navbar-default .navbar-nav > li > a:focus { color: #910811; background-color: transparent; text-decoration: underline; }

.navbar-default .navbar-nav > li > a > i { color: #910811; margin-right: 15px; }

.navbar-default .navbar-nav > li { height: 46px; }

.navbar-default .navbar-nav > li > a { margin-top: 13px; }

.navbar-default .navbar-nav > li > a.button { color: white; padding: 7px 40px !important; margin-top: 0; }

.navbar-default .navbar-nav > li > a.button:hover { color: #910811; text-decoration: none; }

nav.navbar { transition: .2s ease; }

nav.navbar.shrink { padding: 10px 15px; box-shadow: 0 2px 3px rgba(7, 7, 78, 0.6); }

.search-dropdown { display: none; width: 100%; padding: 0 100px; background: rgba(255, 255, 255, 0.9); z-index: 999999999999 !important; position: fixed; top: 0 !important; border-top: 2px solid #d1d1d1; border-bottom: 2px solid #d1d1d1; }

