/***************************************************/
/* TERMS & CONDITIONS                              */
/***************************************************/

.page-template-tos-privacy {
    & .row--padded {
        padding: 1.5rem 0px;
    }
}

#terms-hero {
    & .container-fluid {
        color: white;
        padding: 1.2rem 0;
    }
    & .row--padded:first-child {
        padding: 0;
        background-color: $teal;
    }
    & h1 {
        color: white;
    }
    border-bottom: 10px solid $grey-medium;
}

#user-type {
    border-bottom: 1px solid $grey-medium;

    .row--padded {
        padding: .5rem 0;
    }
    a {
        text-decoration: none;

        &:not(:last-of-type) {
            &:after {
                display: inline-block;
                content: '|';
                color: #666;
                margin-left: 10px;
                margin-right: 10px;
            }
        }

        &.active {
            color: $red;
        }

        &:hover {
            color: $red;
        }
        &:focus {
            color: $red;
        }
    }
}

#accordions {
    min-height: 60vh;
    font-size: 1.5rem;

    .panel-default {
        border: none;
    }
    .panel-default > .panel-heading + .panel-collapse > .panel-body {
        border-top-color: white !important;
        border-bottom-color: white;
    }
    .panel-default > .panel-heading {
        background-color: white;
        border-color: white;
    }
    .panel-heading, .panel-body {
        padding: 0;
    }
    .panel-body > .container {
        padding: 0;
        width: 75%;
        float: left;
        padding-left: 2%;
    }
    .panel-heading .panel-title {
        font-size: 32px;

        &:before {
            font-family: 'FontAwesome';
            content: "\f0d7";
            float: right;
            font-size: 2rem;
            color: $grey-medium;
        }
    }
    h4 {
        color: $grey-medium;
    }
}

.page-template-aboutus footer {
    margin-top: 25px;
    transform: translateY(0px);
    position: relative;
}
